<template>
    <div style="margin-top: 30px;">
        <el-page-header @back="goBack" content="Detail Page"></el-page-header>
        <div style="margin-top: 20px"></div>
        <el-form :label-position="right" label-width="80px" :model="student" style="margin-left: 4cm;">
            <el-row>
                <el-col :xl="8" :xs="12">
                    <el-form-item label="ID">
                        <el-input class="inputStyle" v-model="score.id" disabled clearable></el-input>
                    </el-form-item>
                </el-col>
                <el-col :xl="8" :xs="12">
                    <el-form-item label="Student">
                        <el-input class="inputStyle" v-model="student" clearable></el-input>
                        <el-option
                                    v-for="item in allStudents"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :xl="8" :xs="12">
                    <el-form-item label="Course">
                        <el-input class="inputStyle" v-model="course" clearable></el-input>
                        <el-option
                                    v-for="item in allCourses"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                    </el-form-item>
                </el-col>
                <el-col :xl="8" :xs="12">
                    <el-form-item label="Exam Name">
                        <el-input class="inputStyle" v-model="name" clearable></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-col :xl="8" :xs="12">
                    <el-form-item label="Exam Time">
                        <el-date-picker
                                v-model="score.examTime"
                                :picker-options="pickerOptions"
                                value-format="yyyy-MM-dd"
                                start-placeholder="Start Date">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
           
            <el-row type="flex" class="buttonsStyle">
                <el-col :xl="2" :xs="10">
                    <el-button type="primary" plain @click="updateScoreInfo">Confirm</el-button>
                </el-col>
                <el-col :xl="2" :xs="10">
                    <el-button plain @click="goBack">Cancel</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: 'updateInfo',
        data() {
            return {
                score: '',
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() > Date.now();
                    }
                },
                allStudents: [],
                allCourses: [],
            }
        },
        watch: {
            scoreInfo(v1, v2) {
            },
            allCrouses(v1, v2) {
                console.log(v1);
            },
            allStudents(v1, v2) {
                console.log(v1);
            },
            
            },
        mounted() {
            //初始化学院、专业、班级信息
            this.initStudentCourseScores(true);
            console.log(this.student);
        },
        computed: {
            scoreInfo() {
                this.score = JSON.parse(this.$store.state.updateScore);
            }
        },
        methods: {
            goBack() {
                this.$emit('changeName', 'scoreInfo');
            },
            initStudentCourseScores() {
        // 假设这里有获取学生和课程的相关逻辑
        // 以下是示例，你需要根据你的数据结构和逻辑进行调整

        // 假设this.student和this.course分别是学生和课程的相关数据
        // 你可能有一个数据结构来表示学生和课程的关联，比如一个二维数组或对象

        // 初始化学生和课程的分数信息
        this.student.forEach(student => {
            student.scores = {}; // 初始化学生的分数信息，可以是一个空对象
            this.course.forEach(course => {
                // 在这里根据需要初始化课程的分数信息，可以是一个默认值，比如0或空字符串
                student.scores[course.id] = 0; // 以课程的ID为键，初始化分数为0
            });
        }
    )},
            updateScoreInfo() {
                this.postJsonRequest('/web/scoreInfo/update', this.score).then(resp => {
                    if (resp.data.object) {
                        this.$message({
                            showClose: true,
                            message: resp.data.object,
                            type: 'error',
                            duration: 0
                        });
                    }
                })
            },
        },
    }
</script>

<style scoped>
    .inputStyle {
        width: 300px;
    }
    @media (min-width: 1920px) {
        .buttonsStyle {
            margin-left: 350px;
        }
    }
</style>