    <component v-bind:is="currentName" @changeName="changeName"></component>
</template>

<script>
    import academyInfo from '../../components/AcademyInfo'
    
    export default {
        name: "AcademyManage",
        data() {
            return {
                currentName: 'academyInfo',
            }
        },
        components: {
            academyInfo,
        },
        methods: {
            changeName(name) {
                this.currentName = name;
            }
        },
    }
</script>

